@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Quicksand:wght@400;500&display=swap);
:root {
  --base-color: #938af3;
  --base-color-dark: #4339b3;
  --base-color-fade: rgba(147, 138, 243, .38);

  /* Contrast color */
  --base-contrast: rgba(171, 171, 173, 0.2);
  --base-contrast-op2: rgba(171, 171, 173, 0.4);

  /* Dark Color */
  --base-dark: #302f38; /* #282733 */
  /* --base-contrast: #ffffff11; */
  --base-contrast-mid: #ffffff28;
  --base-contrast-fall: #27272b;
  --base-contrast-fade: #ffffff07;
  --base-white: #ababad;
  --base-grey: #77777e;
  --base-red: #d6471b;
  --base-orange: #ff6347;

  /* Text color */
  --text-black: #55555b;
  --text-white: #dedede;

  --top-bar-height: 56px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 5px;
  background: #ffffff07;
  background: var(--base-contrast-fade);
}

::-webkit-scrollbar-thumb {
  background-color: #ffffff28;
  background-color: var(--base-contrast-mid);
  border-radius: 50px;
}


.logo-kit img {
  width: 30px;
  margin-right: 10px;
}
@font-face {
  font-family: opens;
  src: url(/my-react-chat-app/static/media/opens.3ed9575d.ttf);
}

@font-face {
  font-family: basier;
  src: url(/my-react-chat-app/static/media/basiercircle.c916e24f.otf);
}

* {
  font-family: 'Quicksan', 'Josefin Sans', 'opens', basier;
}


body .app-body {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  position: relative;
  scroll-behavior: smooth;
  margin: 0;
}

body .app-body.day {
  background-color: #ffffff;
  color: var(--text-dark);
}

body .app-body.night {
  background-color: var(--base-dark);
  color: var(--text-white);
}
svg { width: 25px }

.spinner {
    display: inline-block;
    border: 2px solid var(--base-contrast);
    border-top: 2px solid var(--base-red);
    border-radius: 50%;
    width: 20px;
    height: 20px;
    transition:  ease-in-out .3s all;
    -webkit-animation: spin 700ms infinite linear;
            animation: spin 700ms infinite linear;
}

@-webkit-keyframes spin {
    from {
        transform: rotate(0deg);
    } to {
        transform: rotate(360deg);
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    } to {
        transform: rotate(360deg);
    }
}

