.spinner-page {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner-page .spinner span {
    width: 10px;
    height: 10px;
    margin: 5px;
}


.auth-profile--bx {
    padding: .5rem;
}

.auth-profile-img--bx {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: white;
}


aside.chat-list {
    position: relative;
    background: var(--base-contrast-fall);
    height: 100vh;
    box-shadow: 3px 0 5px #ffffff07;
    z-index: 100;
    color: var(--base-white);
    transition: all ease-in-out 300ms;
}

aside.chat-list.float {
    position: fixed;
    left: 0;
    top: 0;
}

aside.chat-list.hide {
    width: 0px;
    overflow-x: hidden;
    transform: translateX(-100px);
}

aside.chat-list .aside-top {
    height: var(--top-bar-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

aside.chat-list .aside-top .search-pane {
    position: relative;
    height: 30px;
    flex: 1 1;
}

aside.chat-list .aside-top > span {
    width: 30px;
    height: 30px;
    margin: auto 7px;
    border-radius: 50%;
    display: grid;
    place-items: center;
    cursor: pointer;
    transition: all ease-in-out .3s;
}

aside.chat-list .aside-top > span svg {
    width: 23px;
    fill: var(--base-white);
    transition: all ease-in-out .3s;
}

aside.chat-list .aside-top > span:hover {
    background-color: var(--base-contrast-fade);
}

aside.chat-list .aside-top > span:hover svg {
    fill: white;
}

aside.chat-list .aside-top .search-pane input {
    width: 100%;
    height: 100%;
    padding-left: 35px;
    border-radius: 2pt;
    border: none;
    background-color: var(--base-contrast-fade);
    outline: none;
    color: white;
}

aside.chat-list .aside-top .search-pane span {
    height: 100%;
    width: 30px;
    position: absolute;
    display: grid;
    place-items: center;
    left: 0;
}

aside.chat-list .aside-top .search-pane span svg {
    width: 20px;
    fill: var(--base-white)
}

aside.chat-list .aside-top .search-pane input:focus ~ span svg {
    fill: white;
}

aside.chat-list .aside-top .search-pane span.close-search {
    top: 0;
}

aside.chat-list .aside-top .search-pane span.close-search div {
    background-color: var(--base-color);
    padding: 3px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}

aside.chat-list .aside-top .search-pane span.close-search svg {
    width: 100%;
    fill: white;
}

aside.chat-list ul {
    list-style-type: none;
    padding: 0;
}

aside.chat-list ul li {
    margin: .5rem;
    padding: .3rem .5rem;
    cursor: pointer;
    display: flex;
    align-items: center;
    border-radius: 8px;
}

aside.chat-list ul li:hover {
    background: var(--base-contrast-fade);
}

aside.chat-list ul li.active,
aside.chat-list ul li.active:hover {
    background-color: var(--base-color);
}

aside.chat-list ul li div.chat-image {
    width: 40px;
    height: 40px;
    background-color: white;
    border-radius: 50%;
    margin-right: 1rem;
    position: relative;
}

aside.chat-list ul li div.chat-image .online-trk {
    position: absolute;
    width: 12px;
    height: 12px;
    bottom: 0;
    right: 0;
    border-radius: 50%;
    background: var(--base-red);
}

aside.chat-list ul li div.chat-image.online .online-trk {
    background: green;
}

aside.chat-list ul li div.chat-person {
    flex: 1 1;
    display: flex;
    flex-flow: nowrap row;
    align-items: center;
}

aside.chat-list ul li div.chat-person .chat-person--name {
    font-size: 1rem;
    font-weight: normal;
    color: white;
    text-transform: capitalize;
}

aside.chat-list ul li div.chat-person .chat-person--typing {
    margin-left: 1rem;
    color: var(--base-orange);
}

aside.chat-list .alert.visibility-hidden {
    display: none;
}

aside.chat-list .aside-bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    box-shadow: 0 -4px 5px #ffffff07;
}

.home-page {
    display: flex;
}

.home-page .chat-list {
    width: 350px;
    max-width: calc(100% - 10px);
    flex-shrink: 0;
    z-index: 101;
}

@media (max-width: 800px) {
    .home-page .chat-list {
        width: 300px;
    }
}

.home-page .chat-pane {
    flex: 1 1;
    position: relative;
    height: 100vh;
    width: 100%;
}

.home-page .chat-pane .top-bar {
    position: absolute;
    top: 0;
    width: 100%;
    height: var(--top-bar-height);
    box-shadow: 0 3px 5px #ffffff07;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: .4rem 1.35rem;
    color: #cdccd1;
    fill: var(--base-white);
    background-color: var(--base-dark);
    z-index: 99;
}

.home-page .chat-pane .top-bar .toggle-bar {
    z-index: 101;
    position: relative;
}

.home-page .chat-pane .top-bar div {
    flex: 1 1;
    padding-left: 2rem;
}

.home-page header {
    position: fixed;
    width: 100vw;
    padding: .4rem 1.2rem;
    box-sizing: border-box;

    display: flex;
    justify-content: space-between;
    align-items: center;

    z-index: 10;
    background-color: white;

}

.home-page header h1 {
    font-size: 36px;
}

.home-page header svg {
    width: 30px;
    margin-right: 20px;
}

.home-page header svg,
.home-page header h1 {
    transition: ease-in-out .3s width, ease-in-out .3s font-size;
}

.home-page header::before {
    content: '';
    position: absolute;
    bottom: -1.5px;
    left: 0;
    background-color: #89da89;
    border-radius: 50px;
    width: 80%;
    height: 3px;
    z-index: 110;

    transform: translateX(-100%);
    transition: transform cubic-bezier(.71, .55, .62, .9) .45s;
}

.home-page header.stick {
    border-bottom: thin solid #d1d4d1;
}

.home-page header.stick::before {
    transform: translateX(0);
}

.home-page header.stick h1 {
    font-size: 32px;
}

.home-page header.stick svg {
    width: 25px;
}

.home-page .recent-chat-box {
    height: 200vh;

    /* background-color: red; */
    padding-top: 200px;
}

.home-page .recent-chat-box .recent-chat-item {
    width: 100%;
    padding: .75rem 1rem;

    display: flex;
    align-items: center;
    cursor: pointer;

    box-sizing: border-box;
}

.home-page .recent-chat-box .recent-chat-item:hover {
    background-color: rgba(137, 218, 137, 0.05);
}

.recent-chat-item .image-bx {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: #f4f4f4;

    margin-right: 20px;
}

.recent-chat-item .chat-main h3 {
    margin: 0;
    font-weight: normal;
    margin-bottom: 3px;
}

.recent-chat-item .chat-main span {
    color: #99a199;
    font-size: .93rem;
}


/* No Chat Selected */
.no-chat-select-bx {
    /* Set the dimension */
    margin-top: var(--top-bar-height);
    padding-bottom: 100px;
    height: calc(100vh - var(--top-bar-height));
    width: 100%;
    /* Center item */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.no-chat-select-bx img {
    max-width: calc(100% - 3rem);
    max-height: calc(100% - 4rem);
    width: 400px;
}

.no-chat-select-bx h4 {
    font-weight: normal;
    color: var(--base-white);
    font-size: x-large;
}

.chat-start-bx {
    display: flex;
    justify-content: center;
    margin-bottom: 110px;
    margin-top: 30vh;
}

.chat-start-bx img {
    width: 400px;
    max-width: calc(100% - 3rem);
    max-height: 400px;
}

/* Chat Room */
.chat-room {
    position: relative;
    margin-top: var(--top-bar-height);
    height: calc(100vh - var(--top-bar-height));
    padding-bottom: 60px;
}

.chat-room .bottom-bar {
    position: fixed;
    bottom: 0;
    height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    box-shadow: 0 -3px 5px #ffffff07;
    background-color: var(--base-contrast-fall);
    z-index: 100;
}

.chat-room .bottom-bar span {
    fill: var(--base-white);
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
}

.chat-room .bottom-bar span:last-child {
    right: 0;
    left: unset;
}

.chat-room .bottom-bar input {
    background-color: transparent;
    width: 100%;
    height: 100%;
    outline: none;
    border: none;
    color: white;
    padding: 10px 60px;
    font-size: 16px;
}

.chat-room .chat-bx {
    height: 100%;
}

.chat-room .chat-bx .content-bx {
    width: 600px;
    max-width: calc(100% - 10px);
    margin: auto;
}

.chat-room .chat-bx .chat-row {
    display: flex;
    flex-flow: column nowrap;
    margin: 1rem;
}

.chat-room .chat-bx .chat-row.auth {
    align-items: flex-end;
}

.chat-room .chat-bx .chat-row.admin {
    align-items: center;
}

.chat-room .chat-bx .chat-row .chat-auth {
    font-size: 12px;
    color: var(--base-light);
    margin-bottom: 5px;
}

.chat-room .chat-bx .chat-item {
    display: block;
    color: #cdccd1;
    background: var(--base-contrast);
    border-radius: .345rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    max-width: calc(100% - 50px);
    justify-self: flex-start;
    padding: 7px 12px;
}

.chat-room .chat-bx .chat-row.auth .chat-item {
    background: var(--base-color);
}

.chat-room .chat-bx .chat-date-bk {
    margin: 1.5rem 0;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.chat-room .chat-bx .chat-date-bk div {
    height: 1px;
    background-color: var(--base-white);
}

.chat-room .chat-bx .chat-date-bk div:nth-child(1) {
    width: 50px;
}

.chat-room .chat-bx .chat-date-bk div:last-child {
    flex: 1 1;
}

.chat-room .chat-bx .chat-date-bk span {
    padding: .2rem .7rem;
}

.chat-room .message-end {
    height: 100px;
}


.scrollBox {
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    position: relative;
}

.scrollBox .scrollBox--loader {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 1rem auto;
    position: absolute;
    width: 100%;
    transition: all ease-in-out .3s;
}

.scrollBox .scrollBox--content {
    min-height: calc(100% + 10px);
    padding-bottom: calc(100vh / 3);
}


.error-fullpage {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100vw;
    cursor: pointer;
}

.error-fullpage .error {
    font-size: calc(3vmin + 10px);
    color: var(--base-red);
    margin: 1rem 0;
    -webkit-animation-name: keyframes1;
            animation-name: keyframes1;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
            animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

@keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

.error-fullpage small {
    color: var(--base-white);
    font-size: calc(2.3vmin + 3px);
}


#join-bx {
    padding-top: 13%;
}

#join-bx form {
    margin: auto;
    width: 300px;
    max-width: calc(100vw- 10px);
}

#join-bx form h2 {
    color: white;
    text-align: center;
    font-weight: normal;
    font-size: 32px;
}

#join-bx form input {
    display: block;
    width: 100%;
    padding: 10px 14px;
    background-color: var(--base-contrast);
    border: none;
    outline: none;
    margin-bottom: 15px;
    color: white;
    font-size: 15px;
    transition: ease-in-out 300ms background;
}

#join-bx form input:focus,
#join-bx form input:hover {
    background-color: var(--base-contrast-mid);
}

#join-bx form div {
    width: 100%;
    display: flex;
    justify-content: center;
}

#join-bx form div button {
    background-color: var(--base-color);
    outline: none;
    border: none;
    padding: .75rem 2rem;
    color: white;
    font-size: 14px;
    cursor: pointer;
}

#join-bx form div button:disabled {
    color: rgba(255, 255, 255, 0.397);
    cursor: default;
}



.form-page {
    padding-top: 10%;
}

.form-page.day {
    color: var(--text-black);
}

.form-page.night {
    color: var(--text-white);
}

@media screen and (max-width: 800px) {
    .form-page {
        padding-top: 13%;
    }
}

.form-page form {
    margin: auto;
    width: 350px;
    max-width: calc(100vw - 40px);
}

.form-page form h2,
.form-page form h3 {
    text-align: center;
    font-weight: normal;
    font-size: 32px;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.form-page form h2 img {
    width: 70px;
}

.form-page form h3 {
    font-size: 18px;
    margin-bottom: 2rem;
}

.form-page form input {
    display: block;
    width: 100%;
    padding: 10px 14px;
    color: var(--text-white);
    background-color: var(--base-contrast);
    border: none;
    border-radius: 2px;
    outline: none;
    margin-bottom: 15px;
    font-size: 15px;
    transition: ease-in-out 300ms background;
}

.form-page.night form input {
    color: var(--text-white);
}


.form-page form input:focus,
.form-page form input:hover {
    background-color: var(--base-contrast-op2);
}

.form-page form div {
    width: 100%;
    display: flex;
    justify-content: center;
}

.form-page form div button {
    background-color: var(--base-color);
    outline: none;
    border: none;
    border-radius: 8px;
    box-shadow: 0 3px 6px 0 var(--base-color-fade);
    padding: .65rem 2rem;
    color: white;
    font-size: 14px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.form-page form div button .spinner {
    margin-left: 10px;
}

.form-page form div button:disabled {
    color: rgba(255, 255, 255, .34);
    cursor: default;
}

.form-page .redirect {
    margin: .5rem 0;
    margin-top: 2rem;
    display: flex;
    justify-content: flex-start;
}

.form-page .redirect a {
    text-decoration: none;
    color: var(--base-orange);
    transition: ease 300ms all;
    margin-left: 10px;
}

.form-page .redirect a:hover {
    cursor: pointer;
    transform: scale(1.03);
}

.form-page .error {
    color: var(--base-red);
    margin: 1rem 0;
    -webkit-animation-name: keyframes1;
            animation-name: keyframes1;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
            animation-timing-function: cubic-bezier(.71,.55,.62,1.57);
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
}

@-webkit-keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

@keyframes keyframes1 {
    0% {
        opacity: 0;
        transform: scaleX(.8);
    }
    50% {
        opacity: 1;
        transform: scaleX(1);
    }
}

.notfound-top {
    position: fixed;
    top: 0;
    width: 100%;
    padding: 1rem 1.2rem;
}

.notfound-top span {
    color: var(--base-white);
    transition: all ease-in-out 300ms;
    cursor: pointer;
}

.notfound-top span:hover {
    color: var(--base-light);
}

.notfound-bx {
    /* Dimension */
    width: 100vw;
    height: 100vh;
    /* Center Content */
    display: flex;
    justify-content: center;
    align-items: center;
    /* */
    font-size: calc(15vmin + 10px);
    letter-spacing: 1rem;
}

.notfound-bx span:nth-child(1),
.notfound-bx span:nth-child(3) {
    animation: upAndDown 500ms ease-in-out infinite alternate-reverse;
    font-size: 2em;
    color: var(--base-color);
}

.notfound-bx span:nth-child(3) {
    -webkit-animation-delay: 300ms;
            animation-delay: 300ms;
}

@-webkit-keyframes upAndDown {
    from {
        transform: translateY(-10px);
    } to {
        transform: translateY(10px);
    }
}

@keyframes upAndDown {
    from {
        transform: translateY(-10px);
    } to {
        transform: translateY(10px);
    }
}

.notfound-bx span:nth-child(2) {
    color: var(--base-contrast-fade);
    /* text-shadow: 0 0 3px var(--base-contrast-mid); */
    /* font-size: 1.5em; */
    transform: scale(3);
    transform-origin: 37% 50%;
    z-index: -1;
}
